import React, { Component } from 'react';
import Test from './Test';

class _404 extends Component {
    render() {
        return (
            <React.Fragment>
                <h1>404 Not Found!</h1>
                <Test></Test>
            </React.Fragment>
        );
    }
}

export default _404;



